/* eslint-disable no-undef */
import { put, call, takeEvery } from 'redux-saga/effects'
import axios from 'axios'

import { API, AUTH } from '../constants'
import { AUTH_REQUEST, authSuccess, authFailed } from '../actions/auth'

const postAuth = params => axios.post(API + AUTH, {
  ...params,
  serial: 'E2:50:3F:31:30:36',
  subsystem: '80906',
  region: 2,
  device: 'web',
  dmodel: 'vewd',
  dver: '1.0',
  cver: '1.0',
  rvideos: 0
})

export default [
  takeEvery(AUTH_REQUEST, function* ({ login, password }) {
    try {
      const request = yield call(postAuth, {
        login,
        password
      })
      debugger
      //yield put(authSuccess(request))
    } catch (error) {
      put(authFailed(error))
      alert('Server error')
    }
  })
]
