import { AUTH_FAILED, AUTH_REQUEST, AUTH_SUCCESS } from '../actions/auth'

const initState = {
  isFetching: false,
  didInvalidate: false,
  data: null,
  token: null,
  error: null
}

export default (state = initState, action) => {
  switch (action.type) {
    case AUTH_REQUEST:
      return {
        ...state,
        isFetching: true,
        didInvalidate: false
      }
    case AUTH_SUCCESS:
      return {
        ...state,
        isFetching: false,
        didInvalidate: false,
        data: action.data,
        token: action.data.token
      }
    case AUTH_FAILED:
      return {
        ...state,
        isFetching: false,
        didInvalidate: true,
        token: null
      }
    default:
      return state
  }
}
