export const AUTH_REQUEST = 'USER/AUTH_REQUEST'

export function authRequest(login, pass) {
  return {
    type: AUTH_REQUEST,
    login,
    pass
  }
}

export const AUTH_SUCCESS = 'USER/AUTH_SUCCESS'

export function authSuccess(userData) {
  return {
    type: AUTH_SUCCESS,
    userData
  }
}

export const AUTH_FAILED = 'USER/AUTH_FAILED'

export function authFailed(error) {
  return {
    type: AUTH_FAILED,
    error
  }
}

export default {
  authRequest,
  authSuccess,
  authFailed
}
