import React, { Component } from 'react'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import styled from 'styled-components'
import { Field, reduxForm } from 'redux-form'

import { Container } from '../components'
import authActions from '../actions/auth'

import logo from '../assets/images/logo.jpg'

const FormContainer = styled(Container)`
  justify-content: center;
  align-items: center;
`

const Panel = styled.div`
  min-width: 300px;
  min-height: 300px;
  background-color: white;
  border-radius: 5px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
`

const Form = styled.form`
  display: flex;
  flex-direction: column;
  margin: 20px 0;
`
const Input = styled(Field)`
  font-family: ${({ theme }) => theme.fontFamily};
  color: ${({ theme }) => theme.grey};
  font-size: 20px;
  padding: 5px;
  margin: 10px 0;
  border-radius: 3px;
  box-shadow: none;
  border: 1px solid ${({ theme }) => theme.grey};
  width: 100%;
`

const Logo = styled.img`
  width: 70px;
  margin-top: 15px;
  text-align: center;
`

const ButtonContainer = styled.div`
  margin-top: 10px;
  width: 100%;
  display: flex;
  flex-direction: column;
`

const SignInButton = styled.button`
  background-color: ${({ theme }) => theme.base};
  color: white;
  font-size: 20px;
  padding: 10px;
  font-family: ${({ theme }) => theme.fontFamily};
  border-radius: 3px;
  border: none;
`

class Login extends Component {
  submitHandler = ({ login, password }) => {
    const { authRequest } = this.props
    authRequest(login, password)
  }

  render() {
    const { handleSubmit, submitting } = this.props
    return (
      <FormContainer>
        <Panel>
          <Logo src={logo} />
          <Form onSubmit={handleSubmit(this.submitHandler)}>
            <Input component="input" name="login" type="text" placeholder="Login" />
            <Input component="input" name="password" type="password" placeholder="Password" />
            <ButtonContainer>
              <SignInButton type="submit" disabled={submitting}>
                Sign In
              </SignInButton>
            </ButtonContainer>
          </Form>
        </Panel>
      </FormContainer>
    )
  }
}

const mapStateToProps = ({ auth }) => ({ auth })
const mapDispatchToProps = dispatch => bindActionCreators({
  ...authActions
}, dispatch)

export default reduxForm({
  form: 'login'
})(connect(mapStateToProps, mapDispatchToProps)(Login))
